@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap);
body {
    margin: 0;
    font-family: 'Rubik',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #222f3e;
    color: #c8d6e5;
}

a{
    color: white;
}

#root {
    text-align: left;
    padding: 32px;
    margin: 0 auto;
    max-width: 1100px;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
}

.won-style {
    background-color: #079992 !important;
}

.lost-style {
    background-color: #b33939 !important;
}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:2rem 1rem;box-shadow:#222222 1px 1px 10px;border-radius:0.2rem;margin-top:1rem}.MatchDetailCard .match-date{margin-top:2px}.MatchDetailCard h3.latest-match{margin-bottom:1rem}.MatchDetailCard .match-venue{margin-top:2px}.MatchDetailCard span{display:block;margin-top:10px}.MatchDetailCard #spn-and{display:block;margin-top:2px;margin-bottom:2px;font-size:12px;font-weight:normal;font-style:normal}.MatchDetailCard .match-result{margin-top:2px}.MatchDetailCard .innings{justify-self:right;text-align:right;margin-right:20px}.MatchDetailCard .h1-innings{margin-top:2px}.MatchDetailCard .p-innings{margin-top:1rem}

.MatchSmallCard{padding:2rem 1rem;box-shadow:#222222 1px 1px 10px;border-radius:0.2rem;justify-self:center}

div.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:0.5fr 1fr 0.2fr 0.4fr;grid-column-gap:1rem;grid-row-gap:1rem}div.TeamPage .team-name{grid-column:span 3;justify-self:start;align-self:end}div.TeamPage .team-name-h1{font-size:4rem;margin-bottom:1rem}div.TeamPage .wins-loss{justify-self:center}div.TeamPage .detail-card{grid-column:span 4}div.TeamPage .old-matches{align-self:end;grid-column:span 4;justify-self:start}div.TeamPage .div-next{margin-left:4rem;-webkit-align-self:center;align-self:center;font-size:1rem}div.TeamPage .pie-chart{max-width:9rem;max-height:9rem;justify-self:center;-webkit-align-self:center;align-self:center;margin-top:1rem}

.MatchPage{display:grid;grid-template-columns:1fr 4fr}.MatchPage .space{height:2rem}.MatchPage .year-selector{justify-self:right;padding:2rem;margin:1rem}

.year-container{color:white;background:#576574;box-shadow:#222222 1px 1px 10px;border-radius:0.2rem;padding:1rem 4rem;margin-top:40px;position:fixed;top:78px;left:170px}.year-container ul{list-style-type:none}.year-container li{padding:0.5rem;text-align:center;font-weight:500;font-size:16px}.year-container li a{color:#c8d6e5}.year-container h3{width:105px;text-align:center}.year-container li.selected{font-weight:700;color:white;font-size:20px}.year-container li.selected a{color:white}

div.HomePage div.team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:1rem;grid-row-gap:1rem}div.HomePage span{font-weight:100;font-size:12px}div.HomePage .appName{font-size:4rem}

.team-component{padding:2rem 2rem;box-shadow:#222222 1px 1px 10px;border-radius:0.2rem;margin-right:1rem;margin-top:1rem;text-align:center;background:#3d5a80}.team-component a{font-weight:500;font-size:1.2rem}

.footer{padding-top:3rem;padding-bottom:3rem}.footer .fm77HeartImg{color:#ff6b6b;font-size:16px}

